@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
* {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  transition: 0.5s ease all;
}
body{
  width: 100vw;
  height: 100vh;
}

:root{
    --heading-color :#4a4a4a ;
    --text-color:rgb(169,169,169);
    --btn-light:rgb(59,128,254);
    --link-light:#73A4FD;
}

.App{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.left__screen{
    display: flex;
    align-items: center;
    padding: 20px;
    width: 40vw;
    height: 100vh;
    background-color: #eee;
    position: fixed;
}
.right__screen{
    position: absolute;
    left: 50%;
    width: 60vw;
    left: 40vw;
}

@media (max-width:800px) {
    .App{
        display: flex;
        flex-direction: column;
    }
    .left__screen{
        position: static;
        width: 100%;
    }
    .right__screen{
        position: static;
        width: 100%;
    }
}
.intro__headings{
    width: 100%;
}
.intro__headings  h2{
      font-size: 40px;
      font-weight: 800;
      color: var(--heading-color);
}

.intro__headings  p{
    margin-top: 20px;
    font-size: 16px;
    color:var(--text-color);
}

.connect__me > ul {
  list-style: none;
  display: flex;
  width: 50%;
  justify-content: space-between;
}

.connect__me  img {
    margin-top: 30px;
    width: 30px;
    height: 30px;
}

.connect__me img :hover {
    fill: red;
}

.fancy__text{
    color: var(--btn-light);
}

.btn{
    padding: 10px 20px;
    margin-top: 20px;
    border: none;
    background-color: var(--btn-light);
    color: white;
    width: 60%;
    font-weight: 600;
}




.full{
    
    width: 100%;
    padding: 50px;
    color: white;
}
/*  Right side Screens */
.first__screen{

    background-color: #f4b93d;
}

.second__screen{
    background-color: #005ba9;
}

.third__screen{
    background-color: tomato;
}

.fourth__screen{
    background-color: #009ee2;

}

/*  CSS for fourth screen */

.csection{
    padding: 10px 20px;
    display: flex;
    justify-content: space-between;
    background-color: rgb(0, 102, 255);
    height: 200px;
    align-items: center;
    margin-top: 50px;
    width: 90%;
}

.csection__info{
  margin-left: 10px;
}

.csection__info > button{
    background-color: var(--btn-light);
    border: none;
    padding: 8px 16px;
    color: white;
    float: right;
}

.footer{
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
    color: white;
}

.personal__info{
    display: flex;
    flex-direction: row;
    margin: 5rem auto;
    overflow: hidden;
}

.personal__info > img {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

.self__intro  > p  {
    line-height:1.3;
    font-family:Arial, Helvetica, sans-serif;
    font-size: 1.9rem;
    margin-bottom: 15px;
    padding: 6px;
    
    
}

mark{
   
   padding: 3px 6px;
   background-color: blueviolet;
   color: white;
  
}

mark:hover{
    cursor:pointer;
    text-decoration:underline;
}
.intro__heading {
    margin-top: 100px;
}

.progress{
    height: 45px;
    background-color: #111;
    color: white;
    border: 1.2em;
    border-radius:20px;
    position: relative;
}

.progress::before{
    content:attr(data-label);
    display:flex;
    align-items: center;
    position: absolute;
    left: 0.5em;
    top:0.5em;
    bottom:0.5em;
    color: black;
    width:calc(0 * 1% );
    width:calc(var(--width,0) * 1% ) ;
    max-width: calc(100% - 1em);
    color: white;
    background-color: #f4b93d;
    padding: 1em;
    border-radius:20px;
}

.front__end, .back__end{
    margin-top: 30px;
    margin-bottom: 20px;
    margin-left: 10px;
    min-width: 40%;
}
.progress__container{
    margin-top: 10px;
}

.skill__interests{
    margin-top: 50px;
}

.interest__section{
    margin-top: 20px;
}

/* .skill__container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
} */
@media (max-width:800px) {
    .full{
        overflow: hidden;
    }
    .personal__info{
        display: flex;
        justify-content: center;
        align-self: center;
       
    }

    .self__intro > p {
        margin-bottom: 5px;
        width: 100%;
        font-size: 24px;
        padding: 4px;
        line-height: 1.5;
    }
    .community__section{
       height: -webkit-fit-content;
       height: -moz-fit-content;
       height: fit-content;
    }
    .csection{
        padding-top:8px;
        padding-bottom: 8px;
        margin-top: 20px;
        display: flex;
        justify-content: center;
        align-items:center;
        flex-direction: column;
        min-height: 280px;
      
    }
    .csection__info > .summary {
       font-size: 12px;
    }

    .csection > img {
         width: 100px;

    }
    .csection__info > button::before{
        content: "Learn More";
        margin-right: 10px;
    }
    .csection__info > button {
        margin-top: 16px;
        width: 100%;
        cursor: pointer;
    }

    .front__end,.back__end{
        margin-top: 30px;
    }

    .front__end >h3 , .back__end > h3 {
        width: 30%;
        border-bottom: 5px solid #f4b93d;
    }
}
